<template>
    <div>
        <el-divider content-position="left"></el-divider>
        <div class="popinfo">
            <DeviceCreatePopInfo></DeviceCreatePopInfo>
        </div>
        <el-form :inline=true ref="device_info" :mode="device_info" label-width="80px" label-position="rigth">
            <el-form-item label="">
                <el-input class="project_input" v-model="device_info.project" placeholder="">
                    <template slot="prepend"></template>
                </el-input>
            </el-form-item><br>
            <el-form-item label="" >
                <el-input class="input-with-select input" placeholder="" v-model="device_info.device_class">
                    <el-select class="select" v-model="device_info.device_class" placeholder="" slot="prepend">
                        <el-option label="Huawei" value="huawei"></el-option>
                        <el-option label="H3c" value="h3c"></el-option>
                        <el-option label="Ruijie" value="ruijie"></el-option>
                        <el-option label="Cisco" value="cisco"></el-option>
                        <el-option label="Linux" value="linux"></el-option>
                    </el-select>
                </el-input>
            </el-form-item>
            <el-form-item>
                <el-input class="input-with-select input" placeholder="" v-model="device_info.area" >
                    <el-select  class="select" placeholder=""  v-model="device_info.area" slot="prepend" >
                        <el-option label="" value="outlet"></el-option>
                        <el-option label="" value="security"></el-option>
                        <el-option label="" value="core"></el-option>
                        <el-option label="" value="convergence"></el-option>
                        <el-option label="" value="access"></el-option>
                        <el-option label="" value="server"></el-option>
                        <el-option label="" value="balance"></el-option>
                        <el-option label="" value="manage"></el-option>
                        <el-option label="" value="monitor"></el-option>
                        <el-option label="" value="value"></el-option>
                    </el-select>
                </el-input>
            </el-form-item><br>
            <el-form-item label=""> 
                <el-input class="input-with-select input" placeholder="" v-model="device_info.port" >
                    <el-select  class="select" placeholder=""  v-model="device_info.protocol" slot="prepend" >
                        <el-option label="SSH" value="ssh"></el-option>
                        <el-option label="TELNET" value="telnet"></el-option>
                        <el-option label="FTP" value="ftp"></el-option>
                        <el-option label="SCP" value="scp"></el-option>
                        <el-option label="TFTP" value="tftp"></el-option>
                    </el-select>
                </el-input>
            </el-form-item>
            <el-form-item >
                    <el-input class="input" v-model="device_info.ip_expression"  placeholder="IP">
                    <template slot="prepend">IP&ensp;&ensp;</template>
                    <el-select  class="checkselect" v-model="device_info.check_protocol" placeholder=""  slot="prepend" >
                        <el-option label="TCP" value="tcp"></el-option>
                        <el-option label="ICMP" value="icmp"></el-option>
                    </el-select>
                    <el-button class="checkbutton" slot="append" icon="el-icon-search" @click="checkip"></el-button>
                    </el-input>
            </el-form-item><br>
            <el-form-item  label="">
                    <el-input class="input" v-model="device_info.username" placeholder="">
                        <template slot="prepend"></template>
                    </el-input>
            </el-form-item>
            <el-form-item >
                    <el-input class="input"  :show-password=true v-model="device_info.password" placeholder="" >
                        <template slot="prepend"></template>
                    </el-input>
            </el-form-item>
            <el-form-item  v-show="device_info.device_class == 'cisco' | device_info.device_class == 'ruijie'">
                    <el-input :show-password=true class="secret_input" placeholder="" v-model="device_info.secret">
                        <template slot="prepend">Secret</template>
                    </el-input>
            </el-form-item><br>
            <el-button class="button" @click="commit"></el-button>
        </el-form>
    </div>
</template>
<script>
import {mapActions, mapMutations} from 'vuex'
import devicecreate_pop_info from '../infopop/devicecreate_pop_info.vue'
export default{
    name:'DeviceCreate',
    components:{
        DeviceCreatePopInfo:devicecreate_pop_info
    },
    methods:{
        ...mapActions('deviceaddAbout',{commit:'commit',checkip:'checkip'}),
        ...mapMutations('deviceaddAbout',{
            PROJECT_POP_INFO:'PROJECT_POP_INFO',
            AREA_POP_INFO:'AREA_POP_INFO',
            IP_EXPRESSION_POP_INFO:'IP_EXPRESSION_POP_INFO',
            PROTOCOL_POP_INFO:'PROTOCOL_POP_INFO',
            PORT_POP_INFO:'PORT_POP_INFO',
                                            }),
    },
    computed:{
        device_info(){
            return this.$store.state.deviceaddAbout.device_info
        },
        project(){
            return this.device_info.project
        },
        device_class(){
            return this.device_info.device_class
        },
        area(){
            return this.device_info.area
        },
        protocol(){
            return this.device_info.protocol
        },
        port(){
            return this.device_info.port
        },
        username(){
            return this.device_info.username
        },
        password(){
            return this.device_info.password
        },
        secret(){
            return this.device_info.secret
        },
        ip_expression(){
            return this.device_info.ip_expression
        },
        check_protocol(){
            return this.device_info.check_protocol
        }
    },
    watch:{
        ip_expression:{
            handler(){
                this.IP_EXPRESSION_POP_INFO()
            }
        },
        port:{
    
            handler(){
            this.PORT_POP_INFO()
            }
        },
        protocol:{
       
            handler(){
            this.PROTOCOL_POP_INFO()
            }
        },
        area:{
            
            handler(){
                this.AREA_POP_INFO()  
            }
        },
        project:{
            handler(){
                this.PROJECT_POP_INFO()
            }
        },
       

    },
    mounted(){        
    }
}
</script>
<style scoped>
    .select{
        width: 100px;
    }
    .input{
        width: 400px;
    }
    .project_input{
        width: 810px;
    }
    .secret_input{
        width: 400px;
        margin-left: 80px;
    }
    .button{
        width: 100px;
        margin-left: 80px;
    }
    .checkbutton{
        width: 60px;
    }
    .checkselect{
        width: 105px
    }   
    .popinfo{
        height: 40px;
    }
</style>